    import uniSearchBar  from '@/components/uni-search-bar/uni-search-bar.vue';
	import QQMapWX from'@/libs/qqmap-wx-jssdk.min.js';
	const qqmapsdk = new QQMapWX({key: 'LXCBZ-NNIKD-UZ64F-H6AFI-UNJLH-OCFGE'})
	let cloneList=[];
	export default {
		data() {
			return {
				footHeight:'250px',
				showBottom:false,
				detail: '',
				map: {
					longitude: 113.76927057974245,
					latitude: 34.76670519464811,
					showLocation: true,
					width: 40,
					height: 100,
					scale: 16
				},
				list: [],				
				oftenList: [],
				address: {
					title: '',
					address: ''
				},
				checked: 0,
				scrollTop: 0,
				top:'110upx',
				mapStatus: 1 // 控制选择地址时 地图不加载附近列表
			}
		},
		components:{
			uniSearchBar
		},
		onLoad(options) {	 
			this.map.latitude = options.latitude;
			this.map.longitude = options.longitude;
			this.reverseGeocoder(options.latitude,options.longitude);
			
		},
		methods: {
			reverseGeocoder(latitude,longitude){
				let _this=this;
				qqmapsdk.reverseGeocoder({
					location: {
						latitude:latitude,
						longitude:longitude
					},
					get_poi: 1,
					poi_options: "page_size=20;page_index=1",
					success: function(e) {						
							_this.setData({
								list: e.result.pois
							})
						setTimeout(() => {
							_this.scrollTop = 1
						}, 1000)
					},
					fail: err => {
						console.log(err)
					}
				})
			},
			addressInput(e){				
				this.footHeight='350px';
				this.showBottom=true;
				this.map.height=50;
				cloneList=[].concat(this.list);				
				this.searchList(e.value);
				
			},
			addressConfirm(e){
				this.searchList(e.value);
			},
			addressCancel(){
				this.map.height=100;
			   this.footHeight='250px';
			   this.showBottom=false;			   
			   this.setData({
				   list:cloneList
				  
			   })								
			},
			handleCancel(){
				uni.navigateBack({
					delta: 1
				})
			},
			handleFinish(){
				uni.redirectTo({
					url: '/pages/index/index?address='+this.address.title+'&latitude='+this.map.latitude+'&longitude='+this.map.longitude
				});
				
			},
			searchList(keywords){
			    let that = this;
				qqmapsdk.getSuggestion({
					keyword: keywords,
					success: (res) => {					
							that.list= res.data				
					},
					fail: err => {
						console.log(err)
					}
				})
			},
			getAddress() {
				let _this = this
				uni.getLocation({
					type: 'gcj02',
					success: (res) => {
						let map = _this.data.map;
						map.longitude = res.longitude;
						map.latitude = res.latitude;
						_this.setData({
							map: map,
							position: {
								longitude: res.longitude,
								latitude: res.latitude
							}
						});

						_this.getAddressList(1)

					},
				})
			},

			//通过地图经纬度查询中文地址以及周边地址
			getAddressList(s = 0) {			
                this.reverseGeocoder(this.position.latitude,this.position.longitude)
				
			},
			//地图移动或者变换就找到中心点经纬度
			mapChange(e) {
				let _this = this;
				clearTimeout(this.timer)
				this.timer = setTimeout(() => {
					if (e.type == 'end') {
						_this.mapCtx = uni.createMapContext('map')
						_this.mapCtx.getCenterLocation({
							success: res => {
								_this.setData({
									position: {
										latitude: res.latitude,
										longitude: res.longitude
									}
								});

								if (_this.mapStatus) { // 防止地图点击时 进行多次加载
									_this.getAddressList(1)
								} else {
									_this.mapStatus = 1
								}
							}
						})
					}
				}, 200)

			},
			bindAddress(index) {
				let list = this.list
				let map = this.map
				map.latitude = list[index].location.lat
				map.longitude = list[index].location.lng
				this.setData({
					map: map,
					checked: index,
					address: list[index],
					mapStatus: 0
				})
			},
			setData(obj) {
				Object.assign(this, obj)
			},


			addressSearch() {
				uni.navigateTo({
					url: '/pages/address-search/address-search'
				})
			},
			
		}
	}
